/*--------------------------
    About Styles  
---------------------------*/
.list-numbers {
    margin-left:50px;
    li {position:relative; list-style-type: none;
        h4 {color:var(--color-secondary);
            font-variant:all-petite-caps;
        font-size:40px;}
    }
    .number {
        position:absolute;
        font-size: 30px;
        left: -50px;
        top:5px;
        opacity: .7;
        font-family: var(--secondary-font);
        font-weight: 500;}
}
.about-style-2 {
    background-color:var(--color-gray);
}
.todo-wrapper {
    display:flex; flex-wrap:wrap;
    justify-content: space-evenly;

    .todo {
        width: 100%;

  min-height: 300px;
  background-size: cover;
  display: block;
  max-width: 450px;
  margin: 10px;
  position:relative;
  border: 3px solid var(--color-secondary);

  .gradient {
    z-index:1;
    width:100%;
    height:100%;
    top:0;
    left:0;
    background: linear-gradient(0deg, rgba(255,255,255,1) 0%, rgba(255,255,255,0.42619054457720584) 42%); 
 }

  span {
    z-index:2;
    position:absolute;
    font-size:120px;
    line-height:1;
    bottom:0px;
    left:0;
    right:0;
    margin:auto;
    text-align:center;
    font-family: var(--primary-font);
    font-weight:800;
    color:var(--color-primary)
  }

    }
}
.about-style-4 {

    .title {
        font-weight: 400;
        margin-bottom: 35px;
        strong {
            color: var(--color-primary);
        }
    }

    .feature-list {
        padding: 0;
        margin: 0;
        list-style: none;
        li {
            display: flex;
            .icon {
                background: var(--color-primary);
                width: 30px;
                height: 30px;
                border-radius: 100%;
                color: var(--color-white);
                text-align: center;
                min-width: 30px;
            }
            .title-wrapper {
                margin-left: 13px;
                .title {
                    font-size: 18px;
                    margin: 0;
                  
                }
                .text {
                    font-size: 16px;
                    margin-bottom: 20px;
                    max-width: 80%;
                    margin-top: 13px;
                }
            }
        }
    }
}


// About Styles 5 

.about-style-5 {
    position: relative;
    z-index: 2;
    .about-wrapper {
        margin-top: -150px;
        background: var(--color-blackest);
        border-radius: 10px;
        padding: 15px;
        @media #{$md-layout} {
            margin-top: 80px;
            padding: 15px 15px 30px 15px;
        }
        @media #{$sm-layout} {
            margin-top: 80px;
            padding: 15px 15px 30px 15px;
        }
        .thumbnail {
            img {
                border-radius: 10px;
            }
        }
        .content {
            .inner {
                padding: 0 40px;
                @media #{$sm-layout} {
                    padding: 0;
                }
                .title {
                    font-size: 30px;
                    margin-bottom: 13px;
                }
                p {
                    margin-bottom: 20px;
                    width: 80%;
                }
            }
        }
    }
}


.contact-address {
    padding: 0;
    margin: 0;
    list-style: none;
    li {
        font-size: 16px;
        svg {
            color: var(--color-primary);
            margin-right: 10px;
        }
    }
}


// About Styles 6
.about-style-6 {

    .rbt-progress-style-1,
    .wrapper {
        position: relative;
        z-index: 2;
        max-width: 850px;
        margin: 0 auto;
        border-radius: 10px;
    }
    .wrapper {
        padding: 50px;
        @media #{$md-layout} {
            padding: 20px;
        }
        @media #{$sm-layout} {
            padding: 20px;
        }
        &::after {
            position: absolute;
            content: "";
            left: 0;
            top: 0;
            background: var(--color-lessdark);
            width: 100%;
            height: 100%;
            z-index: -1;
            opacity: 0.6;
            border-radius: 10px;
        }

    }

    

    .thumbnail {
        img {
            max-height: 200px;
        }
    }

    .subtitle {
        font-size: 18px;
        font-weight: 500;
    }
    .maintitle {
        font-size: 30px;
        line-height: 1.3;
        margin-top: 17px;
        margin-bottom: 25px;
    }
}

